import React, { useEffect } from "react";
import { NextPage } from "next";
import Head from "next/head";
import { UAParser } from "ua-parser-js";
import Prism from "prismjs";

import "../style.css";
import "../prism.css";
import { Header } from "../components/Header";
import { Footer } from "../components/Footer";

interface IOwnProps {
  ua: IUAParser.IResult;
}

const Blog: NextPage<IOwnProps> = ({ ua }) => {
  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="app">
      <Head>
        <title>Dmitriy Gurinenko | Dev Blog</title>
        <meta charSet="utf-8" />
        <meta
          name="viewport"
          content="initial-scale=1.0, width=device-width"
          key="viewport"
        />
        <meta
          name="description"
          content="Dmitriy Gurinenko CV"
          key="description"
        />
        <link rel="icon" type="image/x-icon" href="/static/favicon.ico" />
      </Head>

      <Header />

      <div className="container">
        <div className="text-title">[no posts yet, sorry]</div>
        <br />
      </div>

      <pre style={{ marginBottom: 100 }}>
        <code className="language-javascript">
          {`
            {
              browser: {
                name: ${ua.browser.name},
                version: ${ua.browser.version},
              },
              os: {
                name: ${ua.os.name},
                version: ${ua.os.version},
              },
            }
          `}
        </code>
      </pre>

      {/* <Link href="/blog/[slug]" as="/blog/first-post">
        <a>First Post</a>
      </Link> */}

      <Footer />
    </div>
  );
};

Blog.getInitialProps = async ({ req }) => {
  const userAgent = req ? req.headers["user-agent"] : navigator.userAgent;
  const parser = new UAParser(userAgent);
  return { ua: parser.getResult() };
};

export default Blog;
